'use client';
import { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const StockChart = ({ symbol }) => {
  const [stockData, setStockData] = useState([]);
  const [indicators, setIndicators] = useState(null);
  const [prediction, setPrediction] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchData(symbol);
  }, [symbol]);

  const fetchData = async (selectedSymbol) => {
    setLoading(true);
    setError(null);
    try {
      const [stockRes, indicatorsRes, predictionRes] = await Promise.all([
        fetch(`http://localhost:3000/api/stocks/${selectedSymbol}`),
        fetch(`http://localhost:3000/api/stocks/${selectedSymbol}/indicators`),
        fetch(`http://localhost:3000/api/stocks/${selectedSymbol}/predict`)
      ]);

      if (!stockRes.ok || !indicatorsRes.ok || !predictionRes.ok) {
        throw new Error('Failed to fetch data from one or more endpoints.');
      }

      const stockData = await stockRes.json();
      const indicatorsData = await indicatorsRes.json();
      const predictionData = await predictionRes.json();
      
      setStockData(stockData);
      setIndicators(indicatorsData);
      setPrediction(predictionData);

    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  const chartData = {
    labels: stockData.map(data => data.date).reverse(),
    datasets: [
      {
        label: `Closing Price`,
        data: stockData.map(data => data.close).reverse(),
        borderColor: 'rgb(75, 192, 192)',
        backgroundColor: 'rgba(75, 192, 192, 0.5)',
        tension: 0.1,
      },
    ],
  };

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: `${symbol} Historical Price`,
      },
    },
  };

  return (
    <section className="flex-1 p-6 overflow-auto bg-gray-100 dark:bg-gray-900 transition-colors duration-300">
      <div className="bg-white dark:bg-gray-800 rounded-lg shadow-xl p-6 h-full flex flex-col transition-colors duration-300">
        <h2 className="text-2xl font-bold mb-4 border-b pb-2 border-gray-200 dark:border-gray-700">
          {symbol} Dashboard
        </h2>
        
        {loading && <p className="text-center my-8">Loading stock data...</p>}
        {error && <p className="text-center my-8 text-red-500">Error: {error}</p>}
        {!loading && !error && stockData.length > 0 && (
          <>
            <div className="mb-6 grid grid-cols-1 md:grid-cols-3 gap-4 text-center">
              <div className="bg-gray-50 dark:bg-gray-700 p-4 rounded-lg shadow-md transition-colors duration-300">
                <p className="text-sm font-semibold">52-Week High</p>
                <p className="text-xl font-bold text-green-500">
                  ${indicators?.['52_week_high_low']?.high?.toFixed(2)}
                </p>
              </div>
              <div className="bg-gray-50 dark:bg-gray-700 p-4 rounded-lg shadow-md transition-colors duration-300">
                <p className="text-sm font-semibold">52-Week Low</p>
                <p className="text-xl font-bold text-red-500">
                  ${indicators?.['52_week_high_low']?.low?.toFixed(2)}
                </p>
              </div>
              <div className="bg-gray-50 dark:bg-gray-700 p-4 rounded-lg shadow-md transition-colors duration-300">
                <p className="text-sm font-semibold">Next Day Price Prediction</p>
                <p className="text-xl font-bold text-blue-500">
                  ${prediction?.next_day_price_prediction}
                </p>
              </div>
            </div>
            <div className="flex-1 min-h-0">
              <Line data={chartData} options={options} />
            </div>
          </>
        )}
        {!loading && !error && stockData.length === 0 && (
          <p className="text-center my-8">No data available for this stock.</p>
        )}
      </div>
    </section>
  );
};

export default StockChart;