'use client';
import { useState, useEffect } from 'react';
import Sidebar from '../components/Sidebar';
import StockChart from '../components/StockChart';
import ThemeToggle from '../components/ThemeToggle';

export default function Home() {
  const [selectedSymbol, setSelectedSymbol] = useState('AAPL');
  const [isDarkMode, setIsDarkMode] = useState(false);

  useEffect(() => {
    if (isDarkMode) {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
  }, [isDarkMode]);

  return (
    <div className="flex flex-col h-screen bg-gray-100 dark:bg-gray-900 text-gray-900 dark:text-gray-100 transition-colors duration-300">
      <header className="flex justify-between items-center p-4 shadow-md dark:shadow-none bg-white dark:bg-gray-800 transition-colors duration-300">
        <h1 className="text-2xl font-bold">Stock Dashboard</h1>
        <ThemeToggle isDarkMode={isDarkMode} setIsDarkMode={setIsDarkMode} />
      </header>
      <main className="flex flex-1 overflow-hidden">
        <Sidebar onSelectSymbol={setSelectedSymbol} selectedSymbol={selectedSymbol} />
        <StockChart symbol={selectedSymbol} />
      </main>
    </div>
  );
}