'use client';
import { useState, useEffect } from 'react';

const Sidebar = ({ onSelectSymbol, selectedSymbol }) => {
  const [companies, setCompanies] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchCompanies = async () => {
      try {
        const res = await fetch('http://localhost:3000/api/stocks');
        const data = await res.json();
        setCompanies(data);
      } catch (err) {
        console.error('Failed to fetch companies:', err);
      } finally {
        setLoading(false);
      }
    };
    fetchCompanies();
  }, []);

  return (
    <nav className="w-64 flex-shrink-0 bg-white dark:bg-gray-800 p-4 overflow-y-auto shadow-lg dark:shadow-none transition-colors duration-300">
      <h2 className="text-lg font-semibold mb-4 border-b pb-2 border-gray-200 dark:border-gray-700">Companies</h2>
      <ul>
        {loading ? (
          <p>Loading...</p>
        ) : (
          companies.map((company) => (
            <li
              key={company.symbol}
              className={`p-3 my-2 rounded-lg cursor-pointer transition-colors duration-200 ${
                selectedSymbol === company.symbol
                  ? 'bg-blue-600 text-white shadow-md'
                  : 'hover:bg-gray-200 dark:hover:bg-gray-700'
              }`}
              onClick={() => onSelectSymbol(company.symbol)}
            >
              {company.name} ({company.symbol})
            </li>
          ))
        )}
      </ul>
    </nav>
  );
};

export default Sidebar;